import React,{Component} from 'react';
import {Container,Row,Col} from 'react-bootstrap';
class Section15 extends Component{
    render(){
        return(
            <section id="section15">
    <Container>
       <Row>
           <Col xs="12" sm="8" className=" mx-auto ">
            <Row>
                <Col xs="12" className="section15-column1">
                    <img src="/images/top_shadow.svg" alt="" width="100%" />
                </Col>
            </Row>
            <Row>
                <Col xs="9" sm="10" className="section15-column2" >
                    <img src="/images/open_quotes_logo.svg" width="34%" height="34%"/>
                </Col>
            </Row>
            <Row>
                <Col xs="10" className="mx-auto section15-column3" >
                    <p>“Most landing page builders stick to their own theme, have their own way of doing things, and can’t be easily manipulated for your own purposes. Instapage had everything we needed — it was fully customizable, mobile responsive, and had the analytics and tracking we required for our remarketing efforts.”</p>
                </Col>
            </Row>
            <Row>
                <Col xs="12" className="section15-column4">
                    <p >IAN MAIER, LEAD GENERATION MARKETER</p>

                </Col>
            </Row>
            <Row>
                <Col xs="9" sm="10" className="section15-column5" >
                    <img src="/images/xola_logo.png" />
                </Col>
            </Row>
            <Row>
                <Col xs="12" className="section15-column6">
                    <img src="/images/bottom_shadow.svg" alt="" width="100%" />
                </Col>
            </Row>
        </Col>
       </Row>
    </Container>

</section>
        );
    }
}
export default Section15;