import React,{Component} from 'react';
import {Container,Row,Col} from 'react-bootstrap';

class Section2 extends Component{
    render(){
        return(
            <section id="section2">
    <Container className="section2-column1">
        <Row>
            <Col xs="3" md="2" className="my-auto section2-column2 ">
                <img src="/images/verifone_logo.svg" className="img-fluid" alt=""/>
            </Col>
            <Col xs="3" md="2" className="my-auto section2-column3">
                <img src="/images/truecar_logo.svg" className="img-fluid" alt=""/>
            </Col>
            <Col xs="3" md="2" className="my-auto section2-column4">
                <img src="/images/ebay_logo.svg" className="img-fluid" alt="" />
            </Col>
            <Col xs="3" md="2" className="my-auto section2-column5">
                <img src="/images/verizon_logo.svg" className="img-fluid" alt="" />
            </Col>
            <Col xs="3" md="2" className="my-auto section2-column6">
                <img src="/images/hellofres_logo.svg" className="img-fluid" alt="" />
            </Col>
            <Col xs="3" md="2" className="my-auto section2-column7">
                <img src="/images/okta_logo.svg" className="img-fluid" alt="" />
            </Col>
        </Row>
    </Container>
    </section>
        );
    }
}

export default Section2;